:root {
  --mainBlue: #9fbed8;
  --transparentBlue: rgba(64, 172, 241, 0.7);
  --mainWhite: #ffffff;
  --mainBlack: #333333;
  --mainGrey: rgb(216, 214, 214);
  --mainYellow: #f7d65c;
  --primaryColor: #20639c;
  --offWhite: #f7f7f7;
  --mainBlack: #222;
  --darkGrey: #afafaf;
  --mainTransition: all 0.3s linear;
  --mainSpacing: 0.25rem;
  --lightShadow: 2px 5px 3px 0 rgba(0, 0, 0, 0.25);
  --darkShadow: 4px 10px 5px 0 rgba(0, 0, 0, 0.5);
}
// Colors
$color-primary: #20639c;
$color-secondary: #f7d65c;
$color-white: #fff;
$color-grey: #9e9e9e;
$color-grey-800: #424242;
$color-grey-900: #212121;
$color-pink: #ef0c69;
$color-text: #333333;

// paths
$img-path: "../images";
$font-path: "../../fonts";

//  selection background color
$selection-color: darken($color-secondary, 15%);

//  header
$header-bg: #fff;
$header-padding: 10px 0;

// footer
$footer-bg: #fafafa;
$footer-bottom-bg: #fff;
$footer-padding: 15px 0;

//  brand
//
// brand--colors
$brand-fill: $color-primary;
$brand-fill--light: #fff;
$brand-fill--dark: #000;
// brand--size
$brand-width: 140px !default;
$brand-height: auto;
$brand-xs-width: 80px !default;
$brand-xs-height: auto;
$brand-lg-width: 160px !default;
$brand-lg-height: auto;

//icons
$icons-padding: 8px !default;

// nav-bar
//
$nav-bar--bg: transparent;
$nav-bar--padding: 0;
$nav-bar-space-between: 5px !default;
// menu items
$menu-link-color: #000;
$menu-link-hover: $color-primary;
// sub menu
$sub-menu--bg: $color-secondary;
$sub-menu--padding: 7px 3px;
$overlay-z-index: 10;

